import React from 'react';

import './ListOfGoods.css';

type Props = {
  goods: string[];
};

type State = {
  sortBy: string,
  isReversed: boolean,
};

export class ListOfGoods extends React.Component<Props, State> {
  state: State = {
    sortBy: '',
    isReversed: false,
  };

  getList = () => {
    const { isReversed, sortBy } = this.state;
    const { goods } = this.props;

    let visibleGoods = goods;

    if (isReversed) {
      visibleGoods = [...visibleGoods].reverse();
    }

    if (sortBy) {
      visibleGoods = [...visibleGoods].sort((a, b) => {
        switch (sortBy) {
          case 'alphabet':
            return a.localeCompare(b);
          case 'length':
            return a.length - b.length;
          default:
            return 0;
        }
      });
    }

    return visibleGoods;
  };

  reverseList = () => {
    this.setState((currentState) => ({ isReversed: !currentState.isReversed }));
  };

  sortByAlph = () => {
    this.setState({ sortBy: 'alphabet' });
  };

  sortByLength = () => {
    this.setState({ sortBy: 'length' });
  };

  reset = () => {
    this.setState({
      sortBy: '',
      isReversed: false,
    });
  };

  render() {
    const visibleGoods = this.getList();

    return (
      <div className="goods">
        <div className="goods__buttons">
          <button
            type="button"
            onClick={this.reverseList}
            className="button"
          >
            Reverse
          </button>
          <button
            type="button"
            onClick={this.sortByAlph}
            className="button"
          >
            Sort alphabetically
          </button>
          <button
            type="button"
            onClick={this.reset}
            className="button"
          >
            Reset
          </button>
          <button
            type="button"
            onClick={this.sortByLength}
            className="button"
          >
            Sort by length
          </button>
        </div>
        <ul className="goods__container">
          {visibleGoods.map(good => (
            <li
              key={good}
              className="good"
            >
              {good}
            </li>
          ))}
        </ul>
      </div>
    );
  }
}
